import React, { useState, Fragment } from 'react';
import Button from '@material-ui/core/Button';
import CssBaseline from '@material-ui/core/CssBaseline';
import TextField from '@material-ui/core/TextField';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';

import { KeyboardDatePicker } from '@material-ui/pickers/';
// import { MuiPickersUtilsProvider, InlineDatePicker } from "material-ui-pickers";

import 'date-fns';

import InputLabel from '@material-ui/core/InputLabel';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';

import CampoFuncionario from "../campo-funcionario/CampoFuncionario"
import CampoEpis from "../campo-epis/CampoEpis"
import CampoAutorizador from "../campo-autorizador/CampoAutorizador"


const useStyles = makeStyles(theme => ({
    '@global': {
        body: {
            backgroundColor: theme.palette.common.white,
        },
    },
    paper: {
        marginTop: theme.spacing(8),
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
    },
    avatar: {
        margin: theme.spacing(1),
        backgroundColor: theme.palette.secondary.main,
    },
    form: {
        width: '100%', // Fix IE 11 issue.
        marginTop: theme.spacing(3),
    },
    submit: {
        margin: theme.spacing(3, 0, 2),
    },
    formControl: {
        margin: theme.spacing(0),
        width: '100%',
    },
}));


export default function SignUp() {
    const classes = useStyles();

    const inputLabel = React.useRef(null);
    const [labelWidth, setLabelWidth] = React.useState(0);
    React.useEffect(() => {
        setLabelWidth(inputLabel.current.offsetWidth);
    }, []);

    const [state, setState] = React.useState({
        tipoEpi: '',
        codigoEpi: '',
        funcionario: '',
        motivo: '',
        autorizador: '',
    });

    const [quantidade, setQuantidade] = React.useState('');

    const [open, setOpen] = React.useState(false);

    const handleChangeQuantidade = event => {
        const re = /^[0-9\b]+$/;
        if (event.target.value === '' || re.test(event.target.value)) {
            setQuantidade(event.target.value);
        }
    }


    const handleChange = name => event => {
        setState({
            ...state,
            [name]: event.target.value,
        });
    };

    const handleOpen = () => {
        setOpen(true);
    };

    const handleClose = () => {
        setOpen(false);
    };

    const subTipo = () => {
        if (state.tipoEpi === "1") {
            return (
                <Fragment>
                    <option value="" />
                    <option value={1}>1</option>
                    <option value={2}>2</option>
                    <option value={3}>3</option>
                    <option value={4}>4</option>
                    <option value={5}>5</option>
                    <option value={6}>6</option>
                    <option value={7}>7</option>
                    <option value={8}>8</option>
                </Fragment>
            )
        }
        else {
            return (
                <Fragment>
                    <option value="" />
                </Fragment>)
        }
    }

    const handleSubmit = (event) => {
        event.preventDefault();
        if (state.funcionario !== "" && state.codigoEpi !== "") {
            clearState();
            handleOpen();
            clearSelect();
            saveData();
        }
    }

    const saveData = () => {
        const data = {
            funcionario_id: state.funcionario.value,
            autorizador_id: state.autorizador.value,
            epi_id: state.codigoEpi.value,
            quantidade,
            motivo: state.motivo,
        }

        fetch('https://www.jeancarlos.website/saidaepi', {
            method: 'post',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify(data)
        })
            .then(response => response.json())
    }



    const clearState = () => {
        setState({
            ...state,
            tipoEpi: '',
            codigoEpi: '',
            funcionario: '',
            motivo: '',
            autorizador: '',
        });
        setQuantidade('');
    }

    const clearSelect = () => {

    }

    const handleFuncionario = (value) => {
        setState({
            ...state,
            funcionario: value,
        })
    }

    const handleAutorizador = (value) => {
        setState({
            ...state,
            autorizador: value,
        })
    }

    const handleEpi = (value) => {
        setState({
            ...state,
            codigoEpi: value,
        })
    }


    return (
        <Container component="main" maxWidth="xs">
            <CssBaseline />
            <div className={classes.paper}>
                <Typography component="h1" variant="h5">
                    Entrega de EPI
        </Typography>
                <form className={classes.form} onSubmit={handleSubmit}>
                    <Grid container spacing={2}>
                        <Grid item xs={12}>
                            <CampoFuncionario handleFuncionario={handleFuncionario} funcionario={state.funcionario} />
                        </Grid>
                        {/* <Grid item xs={12}>
                            <FormControl
                                variant={"outlined"}
                                className={classes.formControl}
                                required
                                fullWidth
                                margin="normal"
                                autoComplete="fname">
                                <InputLabel ref={inputLabel} >
                                    Tipo EPI
                                  </InputLabel>
                                <Select
                                    native
                                    required
                                    value={state.tipoEpi}
                                    onChange={handleChange('tipoEpi')}
                                    labelWidth={labelWidth}
                                    inputProps={{
                                        id: 'tipoEpi',
                                    }}>
                                    <option value="" />
                                    <option value={1}>PROTEÇÃO DA CABEÇA</option>
                                    <option value={2}>PROTEÇÃO DOS OLHOS E FACE</option>
                                    <option value={3}>PROTEÇÃO RESPIRATÓRIA</option>
                                    <option value={4}>PROTEÇÃO DO TRONCO</option>
                                    <option value={5}>PROTEÇÃO DOS MEMBROS SUPERIORES</option>
                                    <option value={6}>PROTEÇÃO DOS MEMBROS INFERIORES</option>
                                    <option value={7}>PROTEÇÃO DO CORPO INTEIRO</option>
                                    <option value={8}>PROTEÇÃO CONTRA QUEDAS COM DIFERENÇA DE NÍVEL</option>
                                </Select>
                            </FormControl>
                        </Grid> */}
                        <Grid item xs={9}>
                            <CampoEpis required handleEpi={handleEpi} codigoEpi={state.codigoEpi} />
                        </Grid>
                        <Grid item xs={3} >
                            <TextField
                                value={quantidade}
                                onChange={handleChangeQuantidade}
                                autoComplete="fname"
                                variant="outlined"
                                required
                                fullWidth
                                id="quantidade"
                                label="Quant."
                            />
                        </Grid>
                        <Grid item xs={12} >
                            <FormControl
                                variant={"outlined"}
                                className={classes.formControl}
                                required
                                fullWidth
                                margin="normal"
                                autoComplete="fname">
                                <InputLabel ref={inputLabel} >
                                    Motivo
                                  </InputLabel>
                                <Select
                                    native
                                    value={state.motivo}
                                    onChange={handleChange('motivo')}
                                    labelWidth={labelWidth}
                                    inputProps={{
                                        id: 'motivo',
                                    }}>
                                    <option value="" />
                                    <option value={1}>1) Perda (deixou em local incorreto)</option>
                                    <option value={2}>2) Perda (extravio)</option>
                                    <option value={3}>3) Substituição por uso</option>
                                    <option value={4}>4) Danificado</option>
                                    <option value={5}>5) Recebimento</option>
                                </Select>
                            </FormControl>
                        </Grid>
                        <Grid item xs={12}>
                            <CampoAutorizador handleAutorizador={handleAutorizador} autorizador={state.autorizador} />
                        </Grid>
                    </Grid>
                    <Button
                        type="submit"
                        fullWidth
                        variant="contained"
                        color="primary"
                        className={classes.submit}
                    >
                        Salvar
                </Button>
                    <Dialog
                        open={open}
                        onClose={handleClose}>
                        <DialogTitle id="alert-dialog-title">{"Dados salvos"}</DialogTitle>
                        <DialogContent>
                            <DialogContentText id="alert-dialog-description">
                                Saída de EPI realizada com sucesso
                        </DialogContentText>
                        </DialogContent>
                        <Button onClick={handleClose} color="primary" autoFocus>
                            OK
                        </Button>
                    </Dialog>

                </form>
            </div>

        </Container>
    );
}