import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';

//cadastro de funcionario, entrega de EPI.

const useStyles = makeStyles(theme => ({
    root: {
        flexGrow: 1,
    },
    menuButton: {
        marginRight: theme.spacing(2),
    },
    title: {
        flexGrow: 1,
    },
    menuButtom: {
        padding: '10px',
        cursor: "pointer"
    }
}));

export default function ButtonAppBar({onRouteChange, onLogOut}) {
    const classes = useStyles();

    return (
        <div className={classes.root}>
            <AppBar position="static">
                <Toolbar>
                    <Typography variant="h6" className={classes.title}>
                        <Button color="inherit" onClick = {() => onRouteChange("entrega-de-epi")}>Entrega de EPI</Button>
                        <Button color="inherit" onClick = {() => onRouteChange("registros")}>Registros</Button>
                    </Typography>
                    <Button color="inherit"  onClick={onLogOut}>Sair</Button>
                </Toolbar>
            </AppBar>
        </div>
    );
}