import React from 'react';
import MaterialTable from "material-table"



class Register extends React.Component {

    constructor() {
        super();
        this.state = {
            registros: []
        }
    }

    componentDidMount() {
        fetch('https://www.jeancarlos.website/registros')
            .then(response => response.json())
            .then(({ data }) => {
                this.setState({ registros: data })
            })
    }

    render() {
        return (
            <MaterialTable
                title="Registros"
                columns={[
                    { title: 'Data', field: 'saida' },
                    { title: 'Matricula', field: 'matricula' },
                    { title: 'Nome', field: 'nome' },
                    { title: 'Cargo', field: 'cargo' },
                    { title: 'Código EPI', field: 'codigo_epi' },
                    { title: 'Descrição', field: 'descricao' },
                    { title: 'Quantidade', field: 'quantidade' },
                    { title: 'Motivo', field: 'motivo' },
                    { title: 'Autorizador', field: 'autorizador' },
                ]}
                data={this.state.registros}
                options={{
                    exportButton: true
                }}
                localization={{
                    toolbar: {
                        searchPlaceholder: "Buscar",
                        exportName: "Exportar como .csv",
                        exportTitle: "Exportar"
                    },
                    pagination: {
                        labelRowsSelect: "linhas",
                        labelRowsPerPage: "Linhas por página",
                        firstTooltip: "Primeira página",
                        previousTooltip: "Página anterior",
                        nextTooltip: "Próxima página",
                        lastTooltip: "Ultima página",
                        labelDisplayedRows: "{from}-{to} de {count}",

                    }
                }}
            />
        )
    }
}

export default Register;