import React, {useEffect} from 'react';
import Select from 'react-select';
import { emphasize, makeStyles, useTheme } from '@material-ui/core/styles';

const useStyles = makeStyles(theme => ({
  root:{
  },
  select: {
    fontSize: "1.2em",
  }
}));



export default function CampoEpis({handleEpi, codigoEpi}) {


  const [companiesList, setList] = React.useState([]);


  const classes = useStyles();
  const theme = useTheme();
  const [value, setValue] = React.useState(null);

  const handleChange = value => {
    handleEpi(value)
  };

 

  const funcionarios = () => {
    return fetch('https://www.jeancarlos.website/epis')
      .then(response => response.json())
      .then(({ data }) => {
        return data.map(epi => ({
          value: `${epi.epi_id}`,
          label: `${epi.codigo_epi} ${epi.descricao}`,
        }))
      })
      .then(list => {
        setList(list)
      })
  }

  useEffect(() => {
    funcionarios();
  }, []);



  return (
    <div className={classes.root} >
        <Select className={classes.select}
        value={codigoEpi}
         styles={{
          menu: provided => ({ ...provided, zIndex: 9999 })
        }}
        maxMenuHeight={390}
         theme={(theme) => ({
          ...theme,
          borderRadius: 5,
          colors: {
          ...theme.colors,
            primary25: '#D8D6D9',
            primary: '#3f51b5'
          },
          spacing: {
            baseUnit: 8.5,
            }
        })}

          TextFieldProps={{
            label: 'Funcionario'
          }}
          placeholder="Selecionar EPI"
          options={companiesList}
          onChange={handleChange}
        />
    </div>
  );
}