import React, { useEffect } from 'react';
import Select from 'react-select';
import { emphasize, makeStyles, useTheme } from '@material-ui/core/styles';

const useStyles = makeStyles(theme => ({
    root: {
    },
    select: {
        fontSize: "1.2em",
    }
}));



export default function CampoFuncionario({ handleAutorizador, autorizador }) {


    const [companiesList, setList] = React.useState([]);


    const classes = useStyles();
    const theme = useTheme();
    const [value, setValue] = React.useState(null);

    const handleChange = value => {
        handleAutorizador(value);
    };



    const funcionarios = () => {
        return fetch('https://www.jeancarlos.website/autorizadores')
            .then(response => response.json())
            .then(({ data }) => {
                return data.map(funcionario => ({
                    value: `${funcionario.funcionario_id}`,
                    label: `${funcionario.matricula} ${funcionario.nome}`,
                }))
            })
            .then(list => {
                setList(list)
            })
    }

    useEffect(() => {
        funcionarios();
    }, []);



    return (
        <div className={classes.root}>
            <Select className={classes.select}
                value={autorizador}
                maxMenuHeight={390}
                styles={{
                    menu: provided => ({ ...provided, zIndex: 9999 })
                }}
                theme={(theme) => ({
                    ...theme,
                    borderRadius: 5,
                    colors: {
                        ...theme.colors,
                        primary25: '#D8D6D9',
                        primary: '#3f51b5'
                    },
                    spacing: {
                        baseUnit: 8.5,
                    }
                })}

                TextFieldProps={{
                    label: 'Funcionario'
                }}
                placeholder="Selecionar Autorizador"
                options={companiesList}
                onChange={handleChange}
            />
        </div>
    );
}